import React, { Component } from "react";
import { Row, Col, Menu, Tabs, Button, Dropdown, Popconfirm, Descriptions, } from "antd";
import { SettingOutlined, DownOutlined } from "@ant-design/icons"; //Icons
import { Link } from "react-router-dom"; 

import Comments from "../../components/Core/Comments";
// import Travels from "../../components/Core/Travels";
import Attachments from "../../components/Core/Attachments";
import Bank from "../../components/Core/Bank";
import ProfitLoss from "../../components/Core/ProfitLoss";

import InfoModal from "./Modals/InfoModal";

import { getRecord, delList } from "../../service/projects";

import moment from "moment"
import { fomratDate, formatCurrency, localStore, O_STATUS } from "../../service/constant";

const { Item } = Descriptions;
const { TabPane } = Tabs;

class ProjectInfo extends Component {
    constructor() {
        super();
        this.state = {
            infoModal: false,
            leadId: false,
            data: { },
            basic: {},
            billing: {},
            renderTabs: false,
            permissions: {}
        };
    }
    componentDidMount = ()=>{
        const { proId } = this.props.match.params
        this.getRecord(proId)
    }

    getRecord = (id) =>{
        const { PROJECTS }= JSON.parse(localStore().permissions)
        getRecord(id).then(res=>{
            if(res.success){
                console.log(res.basic);
                this.setState({
                    data: res.data,
                    basic: res.basic,
                    billing: res.billing,
                    leadId: id,
                    infoModal: false,
                    renderTabs: true,
                    permissions: PROJECTS
                })
            }
        })
    }

    closeModal = () => {
        this.setState({ infoModal: false });
    };

    handleDelete = (id) => {
        delList(id).then((res) => {
            if (res.success) {
                this.props.history.push('/Employees')
            }
        });
    };

    callBack = () => {
        const { leadId } = this.state
        this.getRecord(leadId)
    };

    render() {
        const { data, infoModal, leadId, billing, renderTabs, permissions, basic } = this.state;
        const DescTitle = (
            <Row justify="space-between">
                <Col>Project Basic Information</Col>
                <Col>
                    <Dropdown
                        overlay={
                            <Menu>
                                <Menu.Item 
                                    onClick={() => { 
                                        this.setState({ infoModal: true});
                                    }}
                                    disabled={!permissions['UPDATE']}
                                    > Edit </Menu.Item>
                                {(basic && basic.type) === 1 ?  //if condition
                                <Menu.Item> 
                                    <Link
                                        to={{ pathname: `/projects/${leadId}/milestones`, }}
                                        className="nav-link"
                                    >
                                        Milestones
                                    </Link>
                                </Menu.Item>
                                 : //else condition
                                <Menu.Item>
                                    <Link
                                        to={{
                                            pathname: `/projects/${leadId}/milestones/${leadId}/resources`,
                                            // pathname: `/projects/${record.id}/resources`,
                                        }}
                                        className="nav-link"
                                    >
                                        Postions
                                    </Link>
                                </Menu.Item>
                            }
                            </Menu>
                        }
                    >
                        <Button size="small" >
                            <SettingOutlined /> Option <DownOutlined />
                        </Button>
                    </Dropdown>
                </Col>
            </Row>
        );
        return (
            <>
                <Descriptions
                    title={DescTitle}
                    size="small"
                    bordered
                    layout="horizontal"
                    // extra={<Button type="primary">Edit</Button>}
                >
                    <Item label="Project Name">{data.title}</Item>
                    <Item label="Estimated Value">{ formatCurrency(data.value)}</Item>
                    <Item label="Organisation">{
                        data.organizationName ? 
                            <Link
                                to={{
                                    pathname: `/organizations/info/${data.organizationId}`,
                                }}
                                className="nav-link"
                            >
                                {data.organizationName}
                            </Link>
                        : 
                            'No Organisation'
                        
                    }</Item>
                    <Item label="Delegate Contact"> {basic ?basic.ContactName: null}</Item>
                    <Item label="Start date">{data.startDate ? fomratDate(data.startDate): null} </Item>
                    <Item label="End Date">{data.endDate ? fomratDate(data.endDate): null}</Item>
                    <Item label="Bid Date">{data.bidDate ? fomratDate(data.bidDate): null}</Item>
                    <Item label="Status">{basic.status ? O_STATUS[basic.status]: ''}</Item>
                    {/* <Item label="Gender">{data.gender}</Item> */}
                </Descriptions>
                {renderTabs &&(
                    <Tabs
                        type="card"
                        style={{ marginTop: "50px" }}
                        // defaultActiveKey="profitloss"   
                    >
                        <TabPane tab="Comments" key="comments">
                            <Comments targetId={leadId} targetType="WOR" />
                        </TabPane>
                        {/* <TabPane tab="Travels" key="travels">
                            <Travels id={leadId} />
                        </TabPane> */}
                        <TabPane tab="Attachments" key="attachments">
                            <Attachments targetId={leadId} targetType="WOR" />
                        </TabPane>
                        {/* <TabPane tab="Bank Account" key="account">
                            <Bank id={leadId} title={data.name} />
                        </TabPane> */}
                        <TabPane tab="Projected Profit & Loss" key="profitloss">
                            <ProfitLoss id={leadId} billing={billing} />
                        </TabPane>
                    </Tabs>
                )}
                {infoModal && (
                    <InfoModal
                        visible={infoModal}
                        editPro={leadId}
                        close={this.closeModal}
                        callBack={this.callBack}
                    />
                )}
            </>
        );
    }
}

export default ProjectInfo;
